import React from 'react'
import {FaBars} from 'react-icons/fa'
import { Nav, 
    NavbarContainer, 
    Navlogo,
    MobileIcon,
    NavItems,
    NavLinks,
    NavMenu
 } from './NavbarComponents'

const Navbar = ({toggle}) => {
    return (
        <>
        <Nav>
            <NavbarContainer>
                <Navlogo to=".">Abhishek</Navlogo>
                <MobileIcon onClick={toggle}>
                    <FaBars />
                </MobileIcon>
                <NavMenu>
                    <NavItems>
                        <NavLinks to="about">About</NavLinks>
                    </NavItems>
                    <NavItems>
                        <NavLinks to="skils">Skills</NavLinks>
                    </NavItems>
                    <NavItems>
                        <NavLinks to="work">Work Experience</NavLinks>
                    </NavItems>
                    <NavItems>
                        <NavLinks to="contact">Contact Me</NavLinks>
                    </NavItems>
                </NavMenu>
            </NavbarContainer>
        </Nav>
        </>
    )
}

export default Navbar
